














import { Component, Mixins, Prop } from "vue-property-decorator";
import TwoWay from "@/resources/mixins/two-way";
import { quillEditor } from "vue-quill-editor";
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";

// toolbar工具栏的工具选项（默认展示全部）
const toolOptions = [
    ["bold", "italic", "underline", "strike"],
    ["blockquote", "code-block"],
    [{ header: 1 }, { header: 2 }],
    [{ list: "ordered" }, { list: "bullet" }],
    [{ script: "sub" }, { script: "super" }],
    [{ indent: "-1" }, { indent: "+1" }],
    [{ direction: "rtl" }],
    [{ size: ["small", false, "large", "huge"] }],
    [{ header: [1, 2, 3, 4, 5, 6, false] }],
    [{ color: [] }, { background: [] }],
    [{ font: [] }],
    [{ align: [] }],
    ["clean"],
    ["link", "image", "video"]
];
@Component({
    name: "myQuillEdit",
    components: {
        quillEditor
    }
})
export default class myQuillEdit extends Mixins(TwoWay) {
    /** 富文本编辑器高度 */
    @Prop({
        default: "400px"
    })
    height?: string;

    /** 占位文字 */
    @Prop({
        default: "请输入"
    })
    placeholder?: string;

    /** 占位文字 */
    @Prop({
        default: false
    })
    disabled?: boolean;

    loading = false;

    showTag = false;
    quill: any = null;
    get options() {
        const { placeholder } = this;
        return {
            placeholder,
            modules: {
                toolbar: {
                    container: toolOptions, // 工具栏选项
                    handlers: {
                        image: (value: any) => {
                            const quill = (this.$refs.myQuillEditor as any).quill;
                            this.quill = quill;
                            // this.$store.commit("image_upload/set_fileType", 1);
                            // this.showTag = true;
                            // this.$store.commit("image_upload/set_fileLimit", 1);
                        }
                    }
                }
            }
        };
    }
    selected(e: any) {
        const quill = this.quill;
        console.log(quill.getSelection());
        this.loading = true;
        const length = quill.getSelection() ? quill.getSelection().index : 0;
        quill.insertEmbed(length, "image", `${e[0].filePath}`);
        // 调整光标到最后
        quill.setSelection(length + 1);
        this.loading = false;
    }

    mounted() {
        const myQuillEditor = this.$refs.myQuillEditor as any;
        myQuillEditor.quill.enable(false);
        setTimeout(() => {
            //丢掉编辑器焦点并重新启用编辑器
            myQuillEditor.quill.blur();
            myQuillEditor.quill.enable(true);
        }, 500);
        const videoEl = document.querySelector(".ql-video");
        if (videoEl) {
            videoEl.addEventListener("click", () => {
                setTimeout(() => {
                    const tool = document.querySelector(".ql-tooltip.ql-editing");
                    const left: any = (tool as any).style.left.replace("px", "");
                    console.log(left);
                    (tool as HTMLElement).style.left = `${+left + 100}px`;
                });
            });
        }
    }
}
